import React from "react";
import { useStore, useSensorReadings } from "../state";

interface SensorDisplayProps {
  sensorName: string;
  title?: string;
  theme?: "default" | "dark" | "accent";
  showBorder?: boolean;
}

const SensorDisplay: React.FC<SensorDisplayProps> = ({ 
  sensorName, 
  title = sensorName,
  theme = "default",
  showBorder = true
}) => {
  const { client, status } = useStore();
  const { readings, loading } = useSensorReadings(client, sensorName);

  // Theme styles
  const themeStyles = {
    default: {
      container: "bg-white",
      title: "text-gray-800",
      text: "text-gray-600",
      border: showBorder ? "border border-gray-200" : "",
    },
    dark: {
      container: "bg-gray-800",
      title: "text-white",
      text: "text-gray-300",
      border: showBorder ? "border border-gray-700" : "",
    },
    accent: {
      container: "bg-blue-50",
      title: "text-blue-800",
      text: "text-blue-600",
      border: showBorder ? "border border-blue-200" : "",
    }
  };
  
  const styles = themeStyles[theme];

  // Format value display based on type
  const formatValue = (value: any) => {
    if (typeof value === "number") {
      return Number.isInteger(value) ? value : value.toFixed(2);
    }
    if (typeof value === "object") {
      return JSON.stringify(value);
    }
    return value;
  };

  return (
    <div className={`rounded-lg shadow-md p-4 ${styles.container} ${styles.border} transition-all`}>
      <h2 className={`text-lg font-semibold mb-3 ${styles.title}`}>{title}</h2>
      {status === "connected" ? (
        loading && !readings ? (
          <div className="flex items-center justify-center py-4">
            <p className="text-gray-400">Loading...</p>
          </div>
        ) : (
          <div className="space-y-3">
            {readings ? (
              Object.entries(readings).map(([key, value]) => (
                <div key={key} className="flex justify-between items-center py-1 border-b border-gray-100 last:border-0">
                  <span className={`font-medium ${styles.text}`}>{key}:</span>
                  <span className={`${styles.text} font-mono`}>
                    {formatValue(value)}
                  </span>
                </div>
              ))
            ) : (
              <div className="text-gray-400 italic text-center py-3">No data available</div>
            )}
          </div>
        )
      ) : (
        <div className="bg-gray-50 rounded p-3 text-center text-gray-500">
          <span>Please connect to robot first</span>
        </div>
      )}
    </div>
  );
};

export default SensorDisplay;