import React, { useEffect, useRef } from "react";
import { useStore, useStream } from "../state";

interface VideoStreamProps {
  cameraName: string;
  theme?: "default" | "dark" | "accent";
}

const VideoStream: React.FC<VideoStreamProps> = ({ 
  cameraName,
  theme = "default"
}) => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const { streamClient, status } = useStore();
  const stream = useStream(streamClient, cameraName);

  useEffect(() => {
    if (videoRef.current && stream) {
      videoRef.current.srcObject = stream;
    }
  }, [stream]);

  // Theme styles
  const themeStyles = {
    default: {
      container: "bg-white border border-gray-200 hover:border-gray-300 transition-all duration-200",
      title: "text-gray-800 text-lg font-semibold",
      placeholderBg: "bg-gray-50",
      placeholderText: "text-gray-500 text-base",
    },
    dark: {
      container: "bg-gray-800 border border-gray-700 hover:border-gray-600 transition-all duration-200",
      title: "text-gray-200 text-lg font-semibold",
      placeholderBg: "bg-gray-700",
      placeholderText: "text-gray-300 text-base",
    },
    accent: {
      container: "bg-blue-50 border border-blue-200 hover:border-blue-300 transition-all duration-200",
      title: "text-blue-800 text-lg font-semibold",
      placeholderBg: "bg-blue-100",
      placeholderText: "text-blue-600 text-base",
    }
  };
  
  const styles = themeStyles[theme];

  return (
    <div className={`rounded-xl shadow-lg p-6 ${styles.container}`}>
      <h2 className={`mb-4 ${styles.title}`}>
        {cameraName}
      </h2>
      <div className="relative aspect-video rounded-lg overflow-hidden">
        {status === "connected" ? (
          stream ? (
            <video
              ref={videoRef}
              autoPlay
              playsInline
              muted
              className="w-full h-full object-cover"
            />
          ) : (
            <div className={`${styles.placeholderBg} h-full flex items-center justify-center`}>
              <p className={`${styles.placeholderText} px-4 py-2 rounded-full bg-white/10 backdrop-blur-sm`}>Loading...</p>
            </div>
          )
        ) : (
          <div className={`${styles.placeholderBg} h-full flex items-center justify-center`}>
            <p className={`${styles.placeholderText} px-4 py-2 rounded-full bg-white/10 backdrop-blur-sm`}>Please connect to robot first</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default VideoStream;